<template>
    <div>
        <!-- <navbox></navbox> -->
		<img src="../assets/welecome/logo.png" class="logo" alt="">
		<p class="logobottom">库恩柏亚数字艺术制造研究所</p>
        <img src="../assets/art/bg.png" alt class="bgimg">
        <ul>
            <li
                class="title"
                v-for="(item,index) in title"
                :key="index"
                @mouseenter="come(index)"
                @mouseleave="over(index)"
            >
                <router-link :to="item.t2" class="title2">{{item.t}}</router-link>
            </li>
            <div class="text">
                <p class="text1">库恩柏亚 数字艺术 智造研究所</p>
                <p class="text2">QUIMBAYA ART & TECH.</p>
                <div class="text3" ></div>
                <div class="text4">
                    <p>如果变化是自然界中唯一不变的，那么它就是用几何学的语言写出来的。</p>
                    <p>我的很多作品都在颂扬空间和成长背后的模式。通过动感雕塑和可变形的物体，我努力让观众看
                    到隐藏在明显无定形空间中的令人惊讶的结构。</p>
                    <p>虽然艺术常常是幻想的载体，但我的作品是一种邀请，让我更深入地探索我们自己的世界，并发
                    现它是多么惊人。在经历一个令人惊讶的行为时，一个人的惊奇感和愉悦感会因为意识到它是在
                    实际的身体约束下发生而增强。</p>
                    <p>这些作品可以被看作是一种工具，它增强了我们对事实和感知之
                    间有时微妙的关系的认识。</p>
                    <p>如果变化是自然界中唯一不变的，那么它就是用几何学的语言写出来的。</p>
                    <p>我的很多作品都在颂扬空间和成长背后的模式。通过动感雕塑和可变形的物体，我努力让观众看
                    到隐藏在明显无定形空间中的令人惊讶的结构。</p>
                    
                </div>
            </div>
        </ul>
        <footerbox/>
    </div>
</template>

<script>
import navbox from "@/components/nav/nav.vue";
import footer from "@/components/nav/footer.vue";
export default {
    components: {
        navbox: navbox,
        footerbox: footer
    },
    data() {
        return {
            title: [
                { t: "艺术产品", t2: "/home" },
                { t: "智造研究", t2: "/research" },
                { t: "博物馆研究", t2: "/museum" },
                { t: "艺术泛生", t2: "/art" },
                { t: "解决方案", t2: "/programme" },
                { t: "关于柏亚", t2: "/about" },
                { t: "联系我们", t2: "/contact" }
            ],
            imgs: [
                { p: require("../assets/home/bg.png") },
                { p: require("../assets/research/bg.png") },
                { p: require("../assets/museum/bg.png") },
                { p: require("../assets/art/bg.png") },
                { p: require("../assets/programme/bg.png") },
                { p: require("../assets/about/bj.png") },
                { p: require("../assets/contact/bg.png") }
            ]
        };
    },
    methods: {
        come(index) {
            console.log(index);
            let shows = document.querySelectorAll(".title");
            let showss = document.querySelectorAll(".title a");
            for (let i = 0; i < shows.length; i++) {
                shows[i].style.background = "#fff";
                showss[i].style.color = "#000";
                showss[i].style.border = "solid 0px #b0b0b0"
            }
            // shows[index].style.background = "#000"
            showss[index].style.color = "#333";
            let imgs = document.querySelector(".bgimg");
            console.log(this.imgs[index]);
            imgs.src = this.imgs[index].p;
            showss[index].style.border = "solid 1px #333"
        },
        over(index) {}
    }
};
</script>

<style scoped>
.bgimg {
    width: 100%;
    margin-bottom: 49px;
    transition: 3s;
}
.title a{
    /* transition: .3s; */
}
.title {
    width: 130px;
    height: 48px;
    /* border: solid 1px #333; */
    /* font-family: STFangsong; */
    font-size: 20px;
    color: #333333;
    line-height: 48px;
    margin-right: 19px;
    background: #fff;
    transition: 1s;
    margin-bottom: 20px;
}
.title:nth-of-type(1) {
    /* background: #000; */
    /* color: #333; */
    /* border: solid 1px #333; */
}
.title:nth-of-type(1) a {
    color: #333;
    border: solid 1px #333;
}
.title2 {
    display: inline-block;
    width: 130px;
    height: 48px;
}
.text{
    width: 1007px;
    margin: 0 auto;
    text-align: left;
}
.text1{
    font-size: 18px;
	font-weight: normal;
	font-stretch: normal;
	letter-spacing: 0px;
	color: #333333;
    margin-top: 100px;
    
}
.text2{
    font-size: 18px;
	font-weight: normal;
	font-stretch: normal;
	letter-spacing: 0px;
	color: #333333;
    margin-bottom: 30px;
}
.text3{
    width: 329px;
	height: 2px;
	background-color: #000000;
    margin-top: 39px;
}
.text4{
    font-family: noto-font;
	font-size: 16px;
	font-weight: normal;
	font-stretch: normal;
	letter-spacing: 0px;
	color: #333333;
    margin-top: 37px;
    margin-bottom: 80px;
    line-height: 40px;
}
.logo{
	width: 140px;
	margin-top: 30px;
	/* height: 80px; */
}
.logobottom{
	margin-bottom: 30px;
	font-size: 24px;
	font-family: noto-font
}
</style>


