<template>
  <div class="hello" @click="router">
    <img src="../assets/welecome/logo.png" class="logo" alt="">
    <p class="logocn">库恩柏亚 数字艺术 智造研究所</p>
    <p class="logoen">QUIMBAYA ART & TECH.</p>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
    // 获取窗口高低适配背景图
    var height= window.innerHeight ;
    console.log(height);
    var hello = document.querySelector('.hello');
    hello.style.height = height+'px' 
    // setTimeout(()=>{
      // 三秒后自动跳转首页
      // this.$router.push("/home")
    // },3000)
  },
  methods:{
    router(){
      this.$router.push("/index")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  width: 100%;
  height: 1010px;
  background: url(../assets/welecome/bg.png);
}
.logo{
  /* height: 197px; */
  width: 200px;
  margin: 39px auto;
  margin-top: 350px;
}
.logocn{
  /* font-family: STFangsong; */
  font-size: 26px;
  color: #333333;
}
.logoen{
  /* font-family: PingFang-SC-Regular; */
	font-size: 18px;
  color: #333333;
}
</style>



