import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
// 首页
import index from '@/components/index'
import home from '@/components/home'
// 研究
import research from '@/components/research'
// 博物馆 Museum
import museum  from "@/components/museum"
// 艺术泛生 art
import art  from "@/components/art"
// programme 方案
import programme  from "@/components/programme"
// about 关于
import about  from "@/components/about"
// contact  联系我们
import contact  from "@/components/contact"
// homedetailed home艺术产品详细
import homes  from "@/components/details/home"
// museum 博物馆
import museums  from "@/components/details/museum"
// research 研究
import researchs  from "@/components/details/research"
// art 艺术
import arts  from "@/components/details/art"
Vue.use(Router)

export default new Router({
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    { path: '/a', redirect: '/'},
    {
      // 欢迎也页
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      meta:{
        index:1
      }
    },
    {
      // 首页
      path: '/index',
      name: 'index',
      component: index,
      meta:{
        index:2
      },
    },
    {
      // 首页次
      path: '/home',
      name: 'home',
      component: home,
      meta:{
        index:2
      },
    },
    {
      // 研究
      path: '/research',
      name: 'research',
      component: research,
      meta:{
        index:2
      }
    },
    {
      // 博物馆
      path: '/museum',
      name: 'museum',
      component: museum,
      meta:{
        index:2
      }
    },
    {
      // 艺术泛生
      path: '/art',
      name: 'art',
      component: art,
      meta:{
        index:2
      }
    },
    // programme
    {
      // programme 解决方案
      path: '/programme',
      name: 'programme',
      component: programme,
      meta:{
        index:2
      }
    },
    {
      // 关于 about
      path: '/about',
      name: 'about',
      component: about,
      meta:{
        index:2
      }
    },
    {
      // 关于 contact 
      path: '/contact',
      name: 'contact',
      component: contact,
      meta:{
        index:2
      }
    },
    {
      // home 详情页
      path: '/homes/:id',
      name: 'homes',
      component: homes,
      meta:{
        index:2
      }
    },
    {
      // 博物馆 详情页
      path: '/museums/:id',
      name: 'museums',
      component: museums,
      meta:{
        index:2
      }
    },
    {
      // 研究 详情页
      path: '/researchs/:id',
      name: 'researchs',
      component: researchs,
      meta:{
        index:2
      }
    },
    {
      // 艺术 详情页
      path: '/arts/:id',
      name: 'arts',
      component: arts,
      meta:{
        index:2
      }
    }
  ]
})




