<template>
    <div class="home">
        <div class="line">

        </div>
        <nav>
            <router-link to="/a">
                <img src="../../assets/welecome/logo.png" class="logo" alt="">
                <div class="logocntop">
                    <img  class="imgs" src="../../assets/welecome/text.png" alt="">
                    <!-- <p class="logocn">库恩柏亚科技·技术</p>
                    <p class="logoen">QUBIMBAIA ART & TECH.</p> -->
                </div>
            </router-link>
            
            <ul class="nav">

                <li>

                </li>
                
                <router-link to="/index"><li>首页</li></router-link>
                <router-link to="/home"><li>艺术产品</li>
                <!-- <div class="oo"></div> -->
                </router-link>
                <router-link to="/research"><li>智造研究</li></router-link>
                <router-link to="/museum"><li>博物馆研究</li></router-link>
                <router-link to="/art"><li>艺术泛生</li></router-link>
                <router-link to="/programme"><li>解决方案</li></router-link>
                <router-link to="/about"><li>关于柏亚</li></router-link>
                <router-link to="/contact"><li>联系我们</li></router-link>
            </ul>
        </nav>
    </div>
</template>

<script>

export default {
    updated(){
        let nav =  document.querySelectorAll('.nav a')
        let router = this.$route.path
        console.log(router)
        let id = this.$route.params.id;
        if(router == '/home'|'/homes/'+id){
            console.log('等于')
            nav[0].className = ''
            nav[0].style.borderBottom  = "1px solid #000"
        }else{
            console.log("不等于")
        }
    }
}
</script>

<style scoped>
.home{
    max-width: 1224px;
    margin: 0 auto;
}
.logo{
    width: 66px;
    height: 59px;
    margin-top: 10px;
}
.logocntop{
    display: inline-block;
    margin-left: 20px;
    margin-right: 86px; 
}
.logocn{
    /* font-family: STFangsong; */
	font-size: 20px;
    color: #333333;
}
.logoen{
    /* font-family: PingFang-SC-Regular; */
	font-size: 16px;
    color: #333333;
}
.nav{
    display: inline-block;
    vertical-align: 80%;
}
.nav a{
    padding-bottom: 10px;
    font-family: noto-font;
	font-size: 18px;
    color: #333333;
    margin-left: 20px;
    line-height: 40px;
    width: 70px;
}
nav{
    width: 100%;
    /* height: 80px; */
    padding-bottom: 10px;
    position: fixed;
    top: 0;
    z-index: 100;
    left: 0px;
    background: #fff;
    -moz-box-shadow:2px 1px 18px #333333; -webkit-box-shadow:2px 1px 18px #333333; box-shadow:2px 1px 18px #ccc;   
}
.line{
    height: 60px;
}
.ll{
    display: inline-block;
    /* width: 80px; */
    /* height: 1px; */
    /* background: red */
}
.oo{

}
.router-link-active{
    border-bottom:  1px solid #000
}
.imgs{
    width: 250px;
    /* height: 50px; */
    vertical-align: 20%;
    
}
</style>



