        navbox : navbox,
        footerbox : footer
    },
    data(){
        return {
            id:1,
            img:[
                {  
                    t1:'Mazzo di Fiori',
                    t2:"2014 3d Printed Polyamide，10 inches (open edition) & 16 inches (limited edition)…",  
                    t3:"99",
                    t4:"00",
                    t5:require('../assets/home/1.png')
                },
                {
                   t1:'Mazzo di Fiori',
                    t2:"2014 3d Printed Polyamide，10 inches (open edition) & 16 inches (limited edition)…",
                    t3:"29",
                    t4:"00",
                    t5:require('../assets/home/2.png')
