                    t5:require('../assets/home/9.png')
                }
            ],
        }
    },
    methods:{
        come(index){
            console.log(index)
            let shows = document.querySelectorAll('.overbg')
            console.log(shows[index])
            shows[index].style.top = '0'
        },
        over(index){
            console.log(index)
            let shows = document.querySelectorAll('.overbg')
            console.log(shows[index])
            shows[index].style.top = '301px'
        }
    }
