<style scoped>
.home{
    max-width: 100%;
    margin: 0 auto;
}
.homebg{
    width: 100%;
    margin-top: 40px;
}
.life{
    /* font-family: STFangsong; */
	font-size: 16px;
    color: #333;
    margin-top: 69px;
    margin-bottom: -40px;
}
.png{
    width: 337px;
    height: 300px; 
    
}
.pngtop{
    max-width: 1200px;
    margin: 0 auto;
}
.line{
    height: 70px;
}
.pngtop1{
    width: 337px;
	height: 301px;
    position: relative;
    margin-left: 25px;
    margin-top: 100px;
    display: inline-block;
    overflow: hidden;
}
.overbg{
    display: inline-block;
	width: 337px;
	height: 301px;
    background-color: #00000069;
    position: absolute;
    left: 0;
    top: 301px;
    transition: .3s;
}
.overbg1{
    width: 316px;
	height: 275px;
	border: solid 1px #ffffff;
    margin: 10px;
    padding: 19px;
}
.overbgp{
    margin-top: 50px;
    /* font-family: STFangsong; */
	font-size: 20px;
    color: #ffffff;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap
}
.overbgp1{
    margin-top: 26px;
    font-size: 16px;
    font-family: noto-font;
    color: #ffffff;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
