<template>
    <div class="home">
        <navbox></navbox>
        <img class="homebg" src="../assets/contact/bg.png" alt="">
        <div class="lifetop">
            <p class="life">杭州库恩柏亚文化科技有限公司</p>
        </div>
        <ul class="contacttop">
            <li>
                <img src="../assets/contact/1.png" alt="">
                <p class="contact1">热线: 0753-155461344(工作时间)</p>
                <p class="contact2">传真: 021-75826832*656</p>
            </li>
            <li>
                <img src="../assets/contact/2.png" alt="">
                <p class="contact1">手机: 14738005038</p>
                <p class="contact2">邮箱: bebana@qq.com</p>
            </li>
            <li>
                <img src="../assets/contact/3.png" alt="">
                <p class="contact1"><a href="http://www.xuzhen.xin" target="view_window">网址: http//www.xuzhen.xin</a></p>
                <p class="contact2">&nbsp;</p>
            </li>
        </ul>
        <!-- <div class="map">
            <img src="../assets/contact/map.png" alt="">
        </div> -->

        <div class="amap-wrapper">
        <el-amap vid="amap" 
            :center="center" 
            :zoom="zoom"
            :plugin="plugin"
            class="amap-demo">
            <el-amap-marker vid="marker" 
            :position="center" 
            :label="label"
            >
            </el-amap-marker>
            <el-amap-circle  vid="circle"
            :center="center" 
            :radius="radius" 
            fill-opacity="0.2"
            strokeColor="#38f"
            strokeOpacity="0.8"
            strokeWeight="1"
            fillColor="#38f"
            >
            </el-amap-circle>
        </el-amap>
        </div>
<!-- --------------------- 
作者：kindCoder 
来源：CSDN 
原文：https://blog.csdn.net/qq_36111804/article/details/80816144 
版权声明：本文为博主原创文章，转载请附上博文链接！ -->



        <div class="line"></div>
        <footerbox/>
    </div>
</template>

<script>
import navbox from '@/components/nav/nav.vue'
import footer from '@/components/nav/footer.vue'
export default {
    components:{
        navbox : navbox,
        footerbox : footer
    },
    data(){
        const self = this;
        return{
            zoom:17,
            center:[119.9709600000,30.1311700000],
            label:{
                content:'浙江省杭州市富阳区银湖街道受降镇上松街道191号第二幢',
                offset:[50,10]
            },
            radius:100,
            loaded: false,
            plugin: [{
              enableHighAccuracy: true,//是否使用高精度定位，默认:true
              timeout: 100,          //超过10秒后停止定位，默认：无穷大
              maximumAge: 0,           //定位结果缓存0毫秒，默认：0
              convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
              showButton: true,        //显示定位按钮，默认：true
              buttonPosition: 'RB',    //定位按钮停靠位置，默认：'LB'，左下角
              showMarker: true,        //定位成功后在定位到的位置显示点标记，默认：true
              showCircle: true,        //定位成功后用圆圈表示定位精度范围，默认：true
              panToLocation: true,     //定位成功后将定位到的位置作为地图中心点，默认：true
              zoomToAccuracy:true,//定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
              extensions:'all',
              pName: 'Geolocation',
            }]
        }
    },
    methods:{
        addRadius(){
      this.radius+=10;
    }
    }
}
</script>

<style scoped>

.home{
    max-width: 100%;
    margin: 0 auto;
}
.homebg{
    width: 100%;
    margin-top: 40px;
}
.life{
    /* font-family: STFangsong; */
	font-size: 24px;
    letter-spacing: 1px;
	color: #333333; 
}
.lifetop{
    max-width: 1000px;
    text-align: left;
    margin: 68px auto;
    margin-bottom: 60px;
}
.line{
    height: 70px;
}
/*  */
.contacttop{
    max-width: 1000px;
    margin: 0 auto;
    text-align: left;
}
.contacttop img{
    width: 200px;
	height: 127px;
    margin-bottom: 35px;
}
.contacttop li{
    width: 310px;
    text-align: left;
    /* margin-right: px; */
}
.contact1,.contact2{
    /* font-family: STFangsong; */
	font-size: 16px;
    letter-spacing: 1px;
	color: #333333;
    line-height: 32px;
}
.map{
    margin-top: 35px;
}
.amap-wrapper{
    width: 1080px;
    height: 350px;
    margin: 0 auto;
    margin-top: 35px;
}
</style>


